import React, { useRef, useState } from "react";

const App = ({ data, children }: any) => {
  const [openState, setOpenState] = useState(false);
  const openStateToggle = () => {
    openState ? setOpenState(false) : setOpenState(true);
  };
  const boxRef: any = useRef();

  return (
    <>
      <section className=" w-11/12 mx-auto flex items-center relative my-14 py-10">
        <div></div>
        <div className="w-11/12 mx-auto">
          <h1
            className={`text-2xl font-bold mb-10 leading-normal ${data.h1ClassName}`}
          >
            <div className="h-0 relative right-4">
              <span className="">"</span>
            </div>
            <div>
              {data.h1}
              <span className="pl-1">"</span>
            </div>
          </h1>
          <div>{data.content}</div>
          <div>{children}</div>
          {data.p.map((pVal, pIdx) => (
            <>
              <div
                className="overflow-y-hidden h-full"
                style={{
                  height: `${
                    openState ? `${boxRef.current.clientHeight}px` : "5rem"
                  }`,
                  transition: "height 0.5s ease",
                }}
              >
                <p className="leading-normal text-lg" ref={boxRef}>
                  {data.p}
                </p>
              </div>
              <div>
                <div
                  className={`h-0 relative -top-20`}
                  style={
                    openState
                      ? { right: "100rem", transition: "right ease 0s 0.5s" }
                      : { right: "0rem" }
                  }
                >
                  <div className="w-full h-20 bg-gradient-to-t from-white to-transparent"></div>
                </div>
                <div
                  className="p-6 flex justify-center"
                  onClick={() => {
                    openStateToggle();
                  }}
                >
                  {openState ? (
                    <>
                      <span className="font-bold text-gray-700">접기 </span>
                      <i className="fas fa-angle-double-up text-orange-600 flex  items-center pt-px pl-1"></i>
                    </>
                  ) : (
                    <>
                      <span className="font-bold text-gray-700">
                        자세히 보기{" "}
                      </span>
                      <i className="fas fa-angle-double-down text-orange-600 flex  items-center pt-px pl-1"></i>
                    </>
                  )}
                </div>
              </div>
            </>
          ))}
        </div>
      </section>
    </>
  );
};

export default App;
